    margin-top: 60px;
  }
}

.price-card__subtitle {
  margin-bottom: 26px;
}

@media (min-width: 1366px) {
  .price-card__subtitle {
    margin-bottom: 40px;
  }
}

.price-card__price {
  font-size: 72px;
}

@media (min-width: 768px) {
  .price-card__price {
    font-size: 144px;
    list-style: -2%;
  }
}

.price-card__footer {
  display: flex;
  flex-direction: column;
  margin-top: 26px;
  border-top: 1px solid var(--color-dark-grey);
  padding-top: 24px;
}

@media (min-width: 768px) {
  .price-card__footer {
    padding-top: 32px;
    flex-direction: row;
    align-items: center;
  }
}

@media (min-width: 1366px) {
  .price-card__footer {
    padding-top: 5px;
  }
}

@media (min-width: 768px) {
  .price-card__btn {
    flex: 0 0 auto;
    margin-right: 20px;
  }
}

@media (min-width: 768px) {
  .price-card__description {
    padding-right: 65px;
  }
}

@media (min-width: 1366px) {
  .price-card__description {
    padding-right: 250px;
  }
}

.about-us {
  margin-top: 40px;
  margin-left: 15px;
}

@media (min-width: 768px) {
  .about-us {
    margin-left: 30px;
  }
}

@media (min-width: 1366px) {
  .about-us {
    margin-top: 200px;
    margin-left: 90px;
  }
}

.about-us__title {
  color: var(--color-dark);
  padding-top: 5px;
}

.slide {
  padding-top: 65px;
  display: flex;
  flex-direction: column;
}

@media (min-width: 768px) {
  .slide {
    flex-direction: row;
    flex-wrap: wrap;
    margin-right: 25px;
  }
}

.slide__image-wrapper {
  width: 202px;
  height: 202px;
}

@media (min-width: 768px) {
  .slide__image-wrapper {
    flex: 0 0 202px;
  }
}

.slide__info {
  padding-top: 15px;
  width: 301px;
}

@media (min-width: 768px) {
  .slide__info {
    padding-left: 20px;
  }
}

.slide__name {
  color: var(--color-dark);
}

.slide__person-info {
  margin-top: 8px;
  color: var(--color-dark-grey);
}

.slide__review {
  color: var(--color-dark);
  margin-top: 15px;
}

.slider-management {
  display: flex;
  justify-content: center;
}

@media (min-width: 768px) {
  .slider-management {
    margin-top: 50px;
  }
}

@media (min-width: 1366px) {
  .slider-management {
    margin-top: 120px;
  }
}

.slider-management .prev {
  order: 1;
}

.slider-management .next {
  order: 3;
}

.slider-management .slick-dots {
  display: flex;
  align-items: center;
  justify-content: center;
  order: 2;
}

.slider-management .slick-dots li.slick-active button {
  background-color: var(--color-yellow);
  position: relative;
}

.slider-management .slick-dots li.slick-active button::before {
  content: "";
  position: absolute;
  top: -3px;
  left: -3px;
  width: 14px;
  height: 14px;
  border: 1px solid var(--color-yellow);
  border-radius: 50%;
}

.slider-management .slick-dots li {
  list-style: none;
  margin: 0 15px;
}

.slider-management .slick-dots li button {
  font-size: 0;
  width: 8px;
  height: 8px;
  background-color: var(--color-light-grey);
  border-radius: 50%;
}

@media (min-width: 768px) {
  .questions {
    margin: 23px 15px;
  }
}

.questions__wrapper {
  display: flex;
  flex-direction: column;
  padding: 31px 15px 15px;
  background: linear-gradient(180deg, #ffffff 0%, #f4f4f4 100%);
  border-radius: 40px;
}

@media (min-width: 768px) {
  .questions__wrapper {
    padding: 70px 60px 26px;
  }
}

@media (min-width: 1366px) {
  .questions__wrapper {
    flex-direction: row;
    justify-content: center;
    align-items: stretch;
  }
}

.questions__title {
  padding-top: 32px;
}

@media (min-width: 768px) {
  .questions__title {
    margin-bottom: 40px;
  }
}

@media (min-width: 1366px) {
  .questions__title {
    padding-top: 0;
  }
}

.questions__row {
  padding-bottom: 16px;
}

.questions__item {
  border: 1px solid #d5d5d5;
  box-sizing: border-box;
  border-radius: 12px;
  padding: 20px 16px;
  cursor: pointer;
}

.questions__item.is-active .questions__body {
  display: block;
}

.questions__item.is-active .questions__open {
  display: none;
}

.questions__item.is-active .questions__close {
  display: block;
}

@media (min-width: 768px) {
  .questions__item {
    padding-left: 24px;
  }
}

.questions__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.questions__open {
  display: block;
  margin-left: 30px;
  font-size: 28px;
  color: var(--color-dark-grey);
}

.questions__close {
  display: none;
  margin-left: 30px;
  font-size: 28px;
  color: var(--color-dark-grey);
}

.questions__body {
  margin-top: 10px;
  background-color: #fff;
  padding: 5px;
  display: none;
  transition: 0.2s;
  border-radius: 5px;
  transition: 0.3s;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 108px;
  background: radial-gradient(100.78% 106.26% at 33.46% 18.73%, rgba(7, 7, 7, 0.8) 0%, rgba(77, 77, 77, 0.8) 100%);
  backdrop-filter: blur(12px);
  margin-top: 21px;
}

@media (min-width: 768px) {
  .footer {
    margin-top: 410px;
  }
}

@media (min-width: 1366px) {
  .footer {
    margin-top: 194px;
  }
}

.footer__text {
  color: var(--color-white);
  letter-spacing: normal;
}
